.posts-wrapper .ant-card-head-title {
  color: red;
  font-size: 22px;
  text-align: center;
}

.posts-wrapper .ant-card {
  font-family: Helvetica Neue, Helvetica, Nimbus Sans L, Arial, Liberation Sans,
    PingFang SC, Hiragino Sans GB, Source Han Sans CN, Source Han Sans SC,
    Microsoft YaHei, Wenquanyi Micro Hei, WenQuanYi Zen Hei, ST Heiti, SimHei,
    WenQuanYi Zen Hei Sharp, sans-serif;
  color: #444;
  line-height: 1.8;
}

.posts-wrapper h1 h2 h3 h4 {
  margin: 1em 0 0.5em 0;
  font-weight: 600;
  font-family: 'Titillium Web', sans-serif;
  position: relative;
  line-height: 40px;
}

.posts-wrapper h1 {
  color: #399ab2;
  font-size: 22px;
  border-bottom: 2px dotted rgba(53, 86, 129, 0.3);
}

.posts-wrapper h2 {
  color: #fff;
  padding-left: 12px;
  background: #6ebee6 !important;
  text-shadow: 2px 2px 3px #222222;
  font-family: '微软雅黑', '宋体', '黑体', Arial;
  margin-bottom: 5px;
}

.posts-wrapper h3 {
  font-size: 18px;
  color: #6ebee6;
  border-left: 12px solid #6ebee6;
  padding: 5px;
  background-color: #f5f5f5;
}

.posts-wrapper h4 {
  font-size: 14px;
  color: #cd4900;
  border-bottom: 1px solid #cd4900;
}

.posts-wrapper pre {
  font-family: Courier New !important;
  background-color: #f6f8fa;
  word-wrap: break-word;
  white-space: pre-wrap;
  padding: 6px 10px;
  line-height: 19px;
  font-size: 13px;
  overflow: auto;
  border-radius: 3px;
  border-width: 1px 1px 1px 1px;
  border-style: dashed dashed dashed dashed;
  border-color: rgb(170, 170, 170) rgb(170, 170, 170) rgb(170, 170, 170)
    rgb(170, 170, 170);
  border-image: initial;
}

.posts-wrapper pre code {
  border: none !important;
}

.posts-wrapper img {
  border: 3px solid #6ebee6;
  border-radius: 8px;
  padding: 5px;
  width: auto;
  max-width: 100% !important;
}

.posts-wrapper a {
  border-bottom: 1px dotted #399ab2;
}

.posts-wrapper a:hover {
  color: #d03500;
}

.posts-wrapper em {
  background-color: pink;
  font-style: normal;
  font-weight: normal;
}

.posts-wrapper textarea {
  overflow: auto;
}

.posts-wrapper strong {
  font-weight: 600;
}

.posts-wrapper ul {
  padding-left: 0px;
  margin-left: 25px;
}

.posts-wrapper ol {
  padding-left: 30px;
}
